#title {
  text-align: center;
  font-family: arial, sans-serif;
  font-size: 35px;
  padding-top:5px;
}
#cell {
  border: 1px solid #ddd;
  padding: 8px;
  width: 20px;
  height: 20px;
}
#cell:hover,
#cell:focus {
  background-color: #808080;
}

#indRow {
  background-color: #f2f2f2;
}

#outer-div {
  width: 100%;
  text-align: center;
}
#inner-div {
  display: inline-block;
  margin: 0 auto;
  padding: 3px;
}

#nav {
  color: white;
  display: inline-block;
  padding: 10px;
}

#newGame {
  color: white;
  display: inline-block;
  padding: 5px;
}

#navbar {
  background-color: grey;
  width: 100%;
  font-size: 25px;
  text-align: center;
}

#nav:hover,
#nav:focus {
  background-color: black;
}

.diyTable {
  padding-top:10px;
}

button{
  background-color: black;
  height:20px;
  width:20px;
  padding:0px;
  color:white;
  font-size:18px;
  border:none;
}


.missingNumber {
  background-color:black;
}
.newGame {
  padding:5px;
  padding-left:37%;
}

.newGameBtn {
  background-color:black;
  width:100px;
  height:35px;
  line-height:2em;
}

.newGameTxt {
  color:white;
  text-decoration: none;
}

.instructions {
  padding:10px; 
}



/* for phone screens (used chrome dev tools to get it for 5/6/7 PLUS, and X) */
@media screen and (max-width:414px) {
  #navbar {
    font-size:20px;
  }
  #title {
    font-size:30px;
  }
  #cell {
    border: 1px solid #ddd;
    padding: 6px;
    width: 20px;
    height: 20px;
  }
  .guess {
    padding-top:10px;
  }
  .inputBox {
    width:50px;
  }
}

/* for iPad and iPad Pro (according to dev tool pixels) */
@media screen and (max-width:1024px) and (min-width:415px) {
  #cell {
    border: 1px solid #ddd;
    padding: 6px;
    width: 63px;
    height: 63px;
    font-size:35px;
  }
  button{
    font-size:35px;
    height:60px;
    width:60px;
  }
  .diyTable {
    font-size:25px;
  }
  .inputBox {
    font-size:20px;
  }
  .submitBtn {
    font-size:20px;
  }
  .newGameBtn {
    background-color:black;
    width:160px;
    height:40px;
    line-height:2.5em;
  }
  .newGameTxt {
    color:white;
    text-decoration: none;
    font-size:25px;
  }
}